import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot } from '@angular/router';
import { ContextDashboardType } from './context-dashboard.model';
import { ContextDashboardService } from './context-dashboard.service';

@Injectable()
export class GroupDashboardGuard {
  constructor(
    private contextDashboardService: ContextDashboardService
  ) {}

  canActivate(route: ActivatedRouteSnapshot) {
    return this.contextDashboardService.activateDashboards(route, [
      ContextDashboardType.Group
    ]);
  }
}
