import { Component, Inject, ViewChild } from '@angular/core';
import { ICON_LIST } from '@c8y/ngx-components';
import { BsModalRef } from 'ngx-bootstrap/modal';
import {
  ContextDashboard,
  DASHBOARD_THEME_CLASSES,
  WIDGET_HEADER_CLASSES
} from './context-dashboard.model';
import { clone } from 'lodash-es';
import { ContextDashboardService } from './context-dashboard.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-dashboard-detail',
  templateUrl: './dashboard-detail.component.html'
})
export class DashboardDetailComponent {
  @ViewChild('dashboardDetailForm', { static: true }) dashboardDetailForm: NgForm;
  dashboard: ContextDashboard;
  current: ContextDashboard;
  deviceType: string;
  isNamedDashboard: boolean;
  icons: string[];
  filteredIcons: string[];
  styling = {
    themeClass: 'dashboard-theme-light',
    headerClass: 'panel-title-regular'
  };
  possibleStyling = { DASHBOARD_THEME_CLASSES, WIDGET_HEADER_CLASSES };
  result: Promise<ContextDashboard> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });

  readonly DEFAULT_DASHBOARD_MARGIN = 12;
  readonly DEFAULT_DASHBOARD_ICON = 'th';
  readonly DEFAULT_DASHBOARD_PRIORITY = 10000;
  readonly DEFAULT_DASHBOARD_NAME = 'Dashboard';

  private _save;
  private _cancel;

  constructor(
    private modal: BsModalRef,
    @Inject(ICON_LIST) iconList: string[],
    private contextDashboardService: ContextDashboardService
  ) {
    this.icons = iconList;
    this.filteredIcons = iconList;
  }

  ngAfterContentInit(): void {
    const defaultDashboardCfg = {
      name: this.DEFAULT_DASHBOARD_NAME,
      priority: this.DEFAULT_DASHBOARD_PRIORITY,
      icon: this.DEFAULT_DASHBOARD_ICON,
      deviceTypeValue: this.deviceType
    };

    if (this.dashboard) {
      this.current = clone(this.dashboard);
      this.setDashboardStyle();
    } else {
      this.dashboard = defaultDashboardCfg;
      this.dashboardDetailForm.form.markAsDirty();
    }
  }

  save() {
    this.dashboard.classes = { [this.styling.themeClass]: true };
    this.dashboard.widgetClasses = { [this.styling.headerClass]: true };
    this._save(this.dashboard);
  }

  close() {
    this._cancel();
    this.modal.hide();
  }

  getDashboardPreviewStyle() {
    const cssClasses = {};
    cssClasses[this.styling.headerClass] = true;
    cssClasses[this.styling.themeClass] = true;
    return cssClasses;
  }

  selectIcon(icon) {
    this.dashboard.icon = icon;
    this.dashboardDetailForm.form.markAsDirty();
  }

  updateFiltered(term: string) {
    if (term) {
      const search = new RegExp(term, 'i');
      this.filteredIcons = this.icons.filter(val => search.test(val));
    } else {
      this.filteredIcons = this.icons;
    }
  }

  private setDashboardStyle() {
    const allClasses = {
      ...this.dashboard.classes,
      ...this.dashboard.widgetClasses
    };

    const styles = Object.keys(allClasses).map(c => c.split('-').pop());
    styles.forEach(styleName => {
      this.styling.themeClass = this.contextDashboardService.getStyling(
        DASHBOARD_THEME_CLASSES,
        styleName,
        this.styling.themeClass
      );
      this.styling.headerClass = this.contextDashboardService.getStyling(
        WIDGET_HEADER_CLASSES,
        styleName,
        this.styling.headerClass
      );
    });
  }
}
