import { Injectable } from '@angular/core';
import { Action, ActionFactory, gettext, ViewContext } from '@c8y/ngx-components';
import { ActivatedRoute, Router } from '@angular/router';
import { ContextDashboardService } from './context-dashboard.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DashboardDetailComponent } from './dashboard-detail.component';
import { ContextDashboardManagedObject } from './context-dashboard.model';

@Injectable()
export class DashboardActionsFactory implements ActionFactory {
  constructor(
    private router: Router,
    private contextDashboardService: ContextDashboardService,
    private bsModal: BsModalService
  ) {}

  get(activatedRoute: ActivatedRoute): Action[] {
    const context =
      !activatedRoute.parent || activatedRoute.snapshot.data.context
        ? activatedRoute.snapshot.data
        : activatedRoute.parent.snapshot.data;

    return [
      {
        label: gettext('Add dashboard'),
        priority: 2500,
        action: () => this.addDashboard(context),
        disabled: !this.isEnabled(context)
      }
    ];
  }

  async addDashboard(context) {
    let initialState: { deviceType: string };

    if (context.context === ViewContext.Device) {
      initialState = {
        deviceType: context.contextData.type
      };
    }

    const modal = this.bsModal.show(DashboardDetailComponent, {
      class: 'modal-lg',
      initialState,
      ignoreBackdropClick: true
    }).content as DashboardDetailComponent;
    try {
      const dashboardCfg = await modal.result;
      const dashboardMO: ContextDashboardManagedObject = await this.contextDashboardService.create(
        dashboardCfg,
        context
      );
      await this.contextDashboardService.navigateToDashboard(dashboardMO);
      await this.contextDashboardService.refreshTabs(dashboardMO);
      modal.close();
    } catch (ex) {
      // intended empty
    }
  }

  private isEnabled(context) {
    return (
      this.contextDashboardService.hasPermission() &&
      [ViewContext.Device, ViewContext.Group].includes(context.context)
    );
  }
}
