import { Injectable, Inject } from '@angular/core';
import { sortBy, cloneDeep } from 'lodash-es';
import { DynamicComponentService, DynamicComponentDefinition } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { ContextDashboardConfig, CONTEXT_DASHBOARD_CONFIG } from './context-dashboard.model';

@Injectable({
  providedIn: 'root'
})
export class WidgetService {
  constructor(
    private dynamicComponentService: DynamicComponentService,
    private translateService: TranslateService,
    @Inject(CONTEXT_DASHBOARD_CONFIG) public moduleConfig: ContextDashboardConfig
  ) {}

  async getWidgetDefinition(componentId): Promise<DynamicComponentDefinition> {
    return this.dynamicComponentService.getById$(componentId).toPromise();
  }

  getWidgetDefinitions() {
    const translatedComponents = Array.from(this.dynamicComponentService.state).map(cmp => ({
      ...cmp,
      label: this.translateService.instant(cmp.label)
    }));

    return cloneDeep(sortBy(translatedComponents, 'label').filter(this.moduleConfig.widgetFilter));
  }
}
