import { Component, OnDestroy } from '@angular/core';
import { DynamicComponentDefinition, Widget } from '@c8y/ngx-components';
import { clone, cloneDeep, escapeRegExp, get, omit } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { iif, Subject, Subscription, timer } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import {
  ContextDashboardManagedObject,
  WIDGET_CONTENT_CLASSES,
  WIDGET_HEADER_CLASSES,
  ContextWidgetConfig
} from './context-dashboard.model';
import { WidgetService } from './widget.service';
import { ContextDashboardService } from './context-dashboard.service';

@Component({
  selector: 'c8y-widget-config',
  templateUrl: './widget-config.component.html'
})
export class WidgetConfigComponent implements OnDestroy {
  mode: 'config' | 'select' | 'style' = 'select';
  searchResult: DynamicComponentDefinition[];
  components: DynamicComponentDefinition[];
  selected: DynamicComponentDefinition;
  searchChange$ = new Subject();
  searchTerm = '';
  context: any;
  componentLabel: string;
  mo: ContextDashboardManagedObject;
  styling = {
    headerClass: 'panel-title-regular',
    contentClass: 'panel-content-light'
  };
  defaultStyling = {
    headerClass: 'panel-title-regular',
    contentClass: 'panel-content-light'
  };
  possibleStyling = { WIDGET_HEADER_CLASSES, WIDGET_CONTENT_CLASSES };
  current: any;
  widgetConfig: ContextWidgetConfig;
  isUpgrade = false;

  get isEdit() {
    return !!this.current;
  }

  result: Promise<Widget> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });

  private searchSub: Subscription;
  private _save;
  private _cancel;

  constructor(
    private widgetService: WidgetService,
    private modal: BsModalRef,
    public contextDashboardService: ContextDashboardService
  ) {}

  ngAfterContentInit(): void {
    this.components = this.widgetService.getWidgetDefinitions();

    if (this.selected) {
      this.current = clone(this.selected);
      this.select(this.selected, this.isEdit ? 'config' : 'select');
    }

    this.searchSub = this.searchChange$
      .pipe(
        switchMap((event: KeyboardEvent) => iif(() => event.which !== 13, timer(200), timer(10)))
      )
      .subscribe(() => {
        this.search();
      });

    if (this.mo.c8y_Dashboard.classes) {
      this.styling = this.setDefaultStyle({
        ...this.mo.c8y_Dashboard.classes,
        ...this.mo.c8y_Dashboard.widgetClasses,
        ...(this.isEdit ? this.current.data.classes : {})
      });
      this.defaultStyling = this.setDefaultStyle({
        ...this.mo.c8y_Dashboard.classes,
        ...this.mo.c8y_Dashboard.widgetClasses
      });
    }
  }

  save() {
    const { _x, _y, _width, _height } = this.selected.data;

    if (this.widgetConfig && this.widgetConfig.device) {
      const { id, name } = this.widgetConfig.device;
      this.widgetConfig.device = { id, name };
    }

    const widget = {
      _x,
      _y,
      _width,
      _height,
      config: omit(this.widgetConfig, 'settings'),
      title: this.selected.data.title,
      componentId: this.selected.id,
      id: this.isEdit ? this.current.data.id : String(Math.random()).substr(2),
      classes: this.getStyle(),
      ...(!this.isEdit ? this.widgetConfig.settings.widgetDefaults : {})
    } as Widget;

    this._save(widget);
  }

  select(cmp, mode: 'select' | 'config' = 'config') {
    cmp.data = cmp.data || {};
    this.selected = cmp;

    this.isUpgrade = !!get(cmp, 'data.settings.upgrade');
    this.contextDashboardService.formDisabled = this.isUpgrade;

    if (this.isEdit) {
      const { _x, _y, _width, _height, classes, title } = this.current.data;
      this.selected.data = { ...this.selected.data, _x, _y, _width, _height, classes, title };
    }

    this.widgetConfig = cloneDeep(this.composeWidgetConfig(this.selected, this.context));

    this.selected.data.title = this.selected.data.title || cmp.label;
    this.componentLabel = cmp.label;
    this.mode = mode;
  }

  search() {
    if (this.searchTerm.length > 0) {
      this.searchResult = this.components.filter(cmp =>
        new RegExp(escapeRegExp(this.searchTerm.trim()), 'i').test(cmp.label)
      );
    } else {
      this.resetSearch();
    }
  }

  resetSearch() {
    this.searchTerm = '';
    this.searchResult = undefined;
  }

  changeMode(mode: 'config' | 'select' | 'style') {
    this.mode = mode;
  }

  close() {
    this._cancel();
    this.modal.hide();
  }

  getStyle(isPreview = false) {
    const cssClasses = {};
    if (isPreview || !this.isDashboardDefaultStyle(this.styling.headerClass)) {
      cssClasses[this.styling.headerClass] = true;
    }

    if (isPreview || !this.isDashboardDefaultStyle(this.styling.contentClass)) {
      cssClasses[this.styling.contentClass] = true;
    }

    if (isPreview) {
      cssClasses[`dashboard-theme-${this.defaultStyling.contentClass.split('-').pop()}`] = true;
    }

    return cssClasses;
  }

  ngOnDestroy(): void {
    this.contextDashboardService.formDisabled = true;
    if (this.searchSub) {
      this.searchSub.unsubscribe();
    }
  }

  private isDashboardDefaultStyle(className) {
    const allClasses = {
      ...this.mo.c8y_Dashboard.classes,
      ...this.mo.c8y_Dashboard.widgetClasses
    };
    const styles = Object.keys(allClasses).map(cssClass => ({ class: cssClass }));
    const style = this.contextDashboardService.getStyling(
      styles,
      className.split('-').pop(),
      undefined
    );
    return !!style;
  }

  private setDefaultStyle(setClasses) {
    let contentClass = this.styling.contentClass;
    let headerClass = this.styling.headerClass;

    const styles = Object.keys(setClasses).map(c => c.split('-').pop());
    styles.forEach(styleName => {
      contentClass = this.contextDashboardService.getStyling(
        WIDGET_CONTENT_CLASSES,
        styleName,
        contentClass
      );
      headerClass = this.contextDashboardService.getStyling(
        WIDGET_HEADER_CLASSES,
        styleName,
        headerClass
      );
    });

    return { headerClass, contentClass };
  }

  private composeWidgetConfig(selectedComponent, context = {}) {
    const setting = {
      settings: {
        ...selectedComponent.data.settings,
        ...get(selectedComponent.data.settings, 'ng1.options'),
        ...get(selectedComponent.data, 'ng1.options'),
        context,
        dashboardMo: this.mo.c8y_Dashboard
      },
      ...selectedComponent.data.config
    } as ContextWidgetConfig;
    return this.applyTargetIfDeviceDashboard(setting);
  }

  private applyTargetIfDeviceDashboard(widgetConfig: ContextWidgetConfig) {
    const isDeviceType = this.contextDashboardService.isDeviceType(this.mo);

    if (isDeviceType) {
      widgetConfig.settings.hideTarget = isDeviceType;

      const noDeviceTarget = widgetConfig.settings.ng1
        ? widgetConfig.settings.ng1.options.noDeviceTarget
        : widgetConfig.settings.noDeviceTarget;

      if (!noDeviceTarget) {
        widgetConfig.device = {
          id: this.context.id,
          name: this.context.name
        };
      }
    }
    return widgetConfig;
  }
}
