import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { DASHBOARD_THEME_CLASSES, WIDGET_HEADER_CLASSES } from './context-dashboard.model';
import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-appearance-settings',
  templateUrl: './appearance-settings.component.html',
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class AppearanceSettingsComponent {
  @Input()
  themeClass: string = 'dashboard-theme-light';
  @Input()
  headerClass: string = 'panel-title-regular';
  @Input()
  defaultThemeClass: string;
  @Input()
  defaultHeaderClass: string;
  @Output()
  themeClassChange = new EventEmitter<string>();
  @Output()
  headerClassChange = new EventEmitter<string>();

  @Input()
  possibleStylingTheme = DASHBOARD_THEME_CLASSES;

  @Input()
  possibleStylingHeader = WIDGET_HEADER_CLASSES;

  themeClassClick(value) {
    value = this.resetToDefault(value, this.themeClass, this.defaultThemeClass);
    this.themeClass = value;
    this.themeClassChange.emit(value);
  }

  headerClassClick(value) {
    value = this.resetToDefault(value, this.headerClass, this.defaultHeaderClass);
    this.headerClass = value;
    this.headerClassChange.emit(value);
  }

  private resetToDefault(value: string, compareTo: string, defaultValue: string) {
    if (defaultValue && value === compareTo) {
      value = defaultValue;
    }
    return value;
  }
}
